.password {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.input {
    width: 300px;
    margin: 0 auto;
}

.button {
    margin-top: 10px;
    background-color: #3963a2;
    border-radius: 8px;
    position: relative;
    width: 100px;
    height: 38px;

    cursor: pointer;
    border: 0;
    outline: 0;
    color: white;

    &:active {
        background-color: #326cc4;
    }
}